@import url(https://fonts.googleapis.com/css?family=Kanit:400,300&subset=thai,latin);
body {
  margin: 0;
  font-family: "Sukhumvit", -apple-system, "Kanit";

  background-color: gray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  padding: 0;
  margin: 0;
}
.wrapper{display:flex;height:800px;position:absolute;transform:translateX(-100%);justify-content:center;-webkit-animation:fadein 1s forwards;animation:fadein 1s forwards}.item{cursor:pointer;display:flex;flex-direction:row-reverse}.item p{padding:20px;margin:0;background-color:white;width:10vw;font-size:17px}.item-parent{position:relative;margin-left:50px;display:flex;align-items:center}.item-parent:after{position:absolute;content:'';width:25px;height:2px;left:0;top:50%;background-color:white;transform:translateX(-100%)}.item-childrens{display:flex;flex-direction:column;justify-content:center}.item-child{display:flex;align-items:flex-start;justify-content:flex-end;margin:10px 0 10px 0;position:relative}.item-child:before{content:'';background-color:white;width:25px;height:2px;position:absolute;top:50%;transform:translateX(100%)}.item-child:after{content:'';background-color:white;width:2px;right:-25px;height:calc(50% + 23px);top:50%;position:absolute}.item-child:last-child:after{transform:translateY(-100%)}.item-child:only-child:after{display:none}@media (max-width: 320px){.wrapper{height:500px;width:100%;margin:0 10px 0 10px}.item p{margin:0;width:1vw;height:3vh;border-radius:12px;font-size:8px;text-align:center}}span{font-size:30px}@-webkit-keyframes fadein{100%{transform:translateX(40%)}}@keyframes fadein{100%{transform:translateX(40%)}}

.heading {
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: black;
    color: white;
    height: 10vh;
    padding: 20px;
}
